@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body * {
  box-sizing: border-box;
}
.image {
  max-width: 100%;
  min-height: 280px;
  margin-bottom: 25px;
}

.comments-wrapper__comment,
.input-form {
  padding: 0 22px;
  width: 100%;
}

.comments-wrapper {
  max-height: 30%;
  flex: 0 1 auto;
  overflow: scroll;
  margin-bottom: 20px;
}

.comments-wrapper__comment__date {
  display: block;
  margin-bottom: 5px;
  color: #999;
  font-size: 13px;
}

.comments-wrapper__comment__text {
  margin-bottom: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.container {
  display: flex;
  height: 100%;
  flex-flow: column;
  background-color: white;
}

.input-form__name,
.input-form__comment {
  height: 30px;
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #cccccc;
  border-radius: 3px;
  padding: 8px;
}

.input-form__name::-webkit-input-placeholder, .input-form__comment::-webkit-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::-moz-placeholder, .input-form__comment::-moz-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name:-ms-input-placeholder, .input-form__comment:-ms-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::-ms-input-placeholder, .input-form__comment::-ms-input-placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__name::placeholder,
.input-form__comment::placeholder {
  font-size: 13px;
  line-height: 15px;
  padding-left: 3px;
}

.input-form__button {
  width: 100%;
  padding: 8px;
  text-align: center;
  color: white;
  font-size: 13px;
  line-height: 15px;
  border: none;
  background-color: #4997D0;
  border-radius: 3px;
}

@media screen and (min-width: 500px) {
  .image {
    max-height: 320px;
  }
  .input-form {
    width: 480px;
    height: 100%;

  }
  .container {
    position: absolute;
    width: 80%;
    max-width: 900px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    flex-flow: row wrap;
    height: auto;
    padding: 30px;
  }
  .comments-wrapper {
    overflow: auto;
    width: 100%;
    max-height: 350px;
  }
  .comments-wrapper__comment__date:first-of-type {
    margin-top: 5px;
  }
  .input-form {
    padding: 0;
  }
}

@media screen and (min-width: 768px) {
  .comments-wrapper {
    width: 35%;
  }
}
.modal {
  position: fixed;
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  background-color: white;
  z-index: 100;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.modal button {
  border: none;
  background: none;
  position: absolute;
  top: 21px;
  right: 21px;
}
.close-button {
  cursor: pointer;
}

@media screen and (min-width: 500px) {
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    padding: 75px;
    -webkit-animation: keyframes-show-0-2-5 0.5s ease;
            animation: keyframes-show-0-2-5 0.5s ease;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgba(0, 0, 0, 0.7);
  }
}

.gallery {
  flex: 1 1;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  align-content: flex-start;
  width: calc(100% - 40px);
  margin: 0 auto;
}

.img {
  width: 100%;
  height: auto;
  margin-bottom: 30px;
  background: black;
  object-fit: contain;
  cursor: pointer;

}
.gallery__loading {
  margin: 0 auto;
  font-size: 30px;
}

@media screen and (min-width: 500px) {
  .gallery {
    flex-flow: row wrap;
    width: 100%;
  }

  .img {
    width: auto;
  }
}

.footer {
  height: 56px;
  width: calc(100vw - 40px);
  position: relative;
  left: calc(-50vw + 50% + 20px);
  border-top: 1px solid #ccc;
  text-align: center;
}

.footer-copyright {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 56px;
  color: #ccc;
}

.App {
  min-height: 100vh;
  max-width: 960px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  margin: 0 auto;

}

.App-header {
  width: 100%;
}

.App-header__title {
  font-family: Roboto Condensed, serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;
  margin-top: 15px;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;

  color: #000000;
}

